import { Component, Input, Output, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import * as fromRoot from '../../../../app-state/reducers';
import * as startserviceTypes from '../../../../app-state/actions/startservice';
import { SharedService, ServiceStartService } from '../../../../_services';
import { Subject } from 'rxjs/subject'
import 'rxjs/add/operator/takeUntil';

@Component({
    templateUrl: './selectunits.component.html',
    styleUrls: ['./selectunits.component.css']
})
export class SelectUnitsComponent implements OnInit, OnDestroy {

    selectedServiceAddr: any;
    unitList: any[] = new Array<any>();
    selectedUnits: any;
    isUnitNotSelected: boolean = null;
    showUnitTable: boolean = false;
    showPlanDetails: boolean = false;
    isPlanDetailsAvailable: boolean;
    showSameDayEnrollMessage: boolean = true;
    unavailableDates: any;

    nameList:any[];

    destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(private router: Router, private startService: ServiceStartService,
      private sharedService: SharedService, private store: Store<fromRoot.State>, private route: ActivatedRoute) {

        this.selectedUnits = this.sharedService.getSelectedUnits();
    }

    onTabChange(tabVal: string) {
        this.showPlanDetails = (tabVal == 'P');
        this.showUnitTable = (tabVal == 'U');
    }

    onUnitSearch(inData){
        let searchTxt = inData.searchText;
        this.unitList = ('Show All' == searchTxt)?this.sharedService.getUnitList(this.selectedServiceAddr,"START"):
            this.sharedService.searchUnitListWithComma(this.sharedService.getUnitList(this.selectedServiceAddr,"START"),inData.searchString);
    }

    onUnitSelection(inData){
        this.store.dispatch(new startserviceTypes.SaveSelectedUnits(inData))
    }

    onContinue(){
        //this.destroy$.next(false);
        this.store.select(fromRoot.getSelectedUnits).takeUntil(this.destroy$).subscribe(data => {
          if(data.isLoadingSuccess){
            if(data.unitList.length == 0){
               this.isUnitNotSelected = true;
            }else{
               this.isUnitNotSelected = false;
               this.router.navigate(['/protected/customer/service/start/verify.htm']);
            }
            this.destroy$.next(true);
          }
        });
    }

    onBack(){
        this.router.navigate(['/protected/customer/service/start/selectproperty.htm']);
    }


    ngOnInit(){

        this.route.data.subscribe(data => {
            this.unitList = data.selectUnitsData.unitList;
            this.selectedServiceAddr = data.selectUnitsData.selectedServiceAddr;
            this.unavailableDates = data.selectUnitsData.unavailableDates;
            this.isPlanDetailsAvailable = data.selectUnitsData.isPlanDetailsAvailable;
        })

        this.nameList = [
          {name:"Service Address", crumbStatus:"completed",barStatus:"active"},
          {name:"Units", crumbStatus:"current",barStatus:"active"},
          {name:"Submit Request", crumbStatus:"",barStatus:""},
          {name:"Confirmation", crumbStatus:"",barStatus:"false"}
        ]
    }

    ngOnDestroy(){
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }
}



