import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { EffectsModule } from "@ngrx/effects";
import { HttpModule } from '@angular/http';
import { SelectPropertyComponent, SelectUnitsComponent, VerifyComponent, ConfirmComponent, SSAddrInfoComponent, SearchComponent,
  UnitTableComponent } from './index';
import { MFStartServiceRoutingModule } from './startservice-routing.module'
import { SharedModule } from '../../../shared/shared.module';
import { MFServiceSahredModule } from '../shared/shared.module';
import { SelectUnitsResolve, ConfirmStartResolve } from '../../../_resolvers';

@NgModule({
    imports: [
        CommonModule,
        HttpModule,
        FormsModule,
        ReactiveFormsModule,
        MFStartServiceRoutingModule,
        SharedModule,
        MFServiceSahredModule,
     ],
    declarations: [
      SelectPropertyComponent,
      SelectUnitsComponent,
      VerifyComponent,
      ConfirmComponent,
      SSAddrInfoComponent,
      SearchComponent,
      UnitTableComponent,
      ],
    exports: [ SelectPropertyComponent, SelectUnitsComponent, VerifyComponent, ConfirmComponent ],
    providers:[ SelectUnitsResolve, ConfirmStartResolve ]
})
export class MFStartServiceModule { }



