import { Component, Input, Output, OnInit, EventEmitter, SimpleChanges, OnChanges } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../../../../_services/user.service'
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import * as fromRoot from '../../../../app-state/reducers';
import * as startserviceTypes from '../../../../app-state/actions/startservice';
import * as moment from 'moment';
import { SortService } from '../../../../_services'
import * as _ from 'lodash'

declare var jQuery: any;

@Component({
    selector: 'unit-table',
    templateUrl: './table.component.html',
    styleUrls: ['table.component.css']

})
export class UnitTableComponent implements OnInit, OnChanges {

    @Input() unitList: any;
    @Output() onUnitCheckClick = new EventEmitter<any>();
    serviceType: string = "START";
    @Input() unavailableDates: string;
    unitSelected: boolean = false;

    constructor(private sortService: SortService) {}

    onUnitCheckEmit(e){
        this.unitSelected = true;
        this.onUnitCheckClick.emit(e)
    }

    ngOnChanges(changes: SimpleChanges){
        if(changes['unitList']){
          if(undefined != this.allItems && this.allItems.length > 0){
            this.allItems = this.unitList;
            this.totalSize = this.allItems.length;
            this.unitList = this.allItems.slice(0, 25);
          }
        }
    }

    //for pagination
    allItems: any[];
    totalSize: any;

    onPagination(pagerObj: any){
        this.unitList = this.allItems.slice(pagerObj.rangeStart-1, pagerObj.rangeEnd+1);
    }

    onSorted(event: any){
       this.sortService.sort(this.unitList,event.sortColumn,event.sortDirection);
    }

    ngOnInit(){
        this.allItems = this.unitList;
        this.totalSize = this.allItems.length;
        this.unitList = this.allItems.slice(0, 25);
    }

}



