import { Component, Input, Output, OnInit, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Store } from '@ngrx/store';
import * as fromRoot from '../../../app-state/reducers';
import * as startserviceTypes from '../../../app-state/actions/startservice';

@Component({
    selector: 'sameday-enroll-message',
    template: `
          <div *ngIf="showMessage" id="errorMessage" >
            <span class="errorInfo"><sdl-message key={{key}}></sdl-message></span>
            <span class="errorIcon"></span>
          </div>
        `
    //styles: [``]
})
export class SameDayEnrollMessageComponent implements OnInit, OnChanges {

    @Input() message: string;
    @Output() isSameDayEnroll = new EventEmitter<boolean>();
    showMessage: boolean = false;
    @Input() fromPage: string;
    key:string;

    constructor(private store: Store<fromRoot.State>){}

    ngOnChanges(chnages: SimpleChanges){

        this.store.select(fromRoot.getNearto5PMflag).subscribe(flagVal => {
            this.showMessage = flagVal;
            this.isSameDayEnroll.emit(flagVal)
            if(flagVal){
                this.key = (this.fromPage == 'units')?'gmess_same_day_enroll_tctxt':'gmess_sameday_enroll_err_tctxt'
            }else{
                //console.log("Checked GetNearTo5PMFlag:::::::value:::"+flagVal)
            }
        })
    }

    ngOnInit(){
      if(this.fromPage == 'units'){
          this.store.dispatch(new startserviceTypes.GetNearTo5PMFlag("selectUnits"));
      }else{
          this.store.dispatch(new startserviceTypes.GetNearTo5PMFlag("Verify"));
      }


   }
}



