import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SelectPropertyComponent, SelectUnitsComponent, VerifyComponent, ConfirmComponent } from './index';
import { SelectUnitsResolve, ConfirmStartResolve } from '../../../_resolvers';

const routes: Routes = [
  { path: 'selectproperty.htm', component: SelectPropertyComponent },
  { path: 'selectunits.htm',
    component: SelectUnitsComponent,
    resolve: {
      selectUnitsData: SelectUnitsResolve
    }
  },
  { path: 'verify.htm', component: VerifyComponent },
  { path: 'confirm.htm',
    component: ConfirmComponent,
    resolve: {
        data: ConfirmStartResolve
    }
  }
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class MFStartServiceRoutingModule { }



