import { Component, Input, Output, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import * as fromRoot from '../../../app-state/reducers';
import * as startserviceTypes from '../../../app-state/actions/startservice';

@Component({
    selector: 'mf-plan-details',
    templateUrl: './plandetails.component.html',
    styles: [`
      .planDetails { padding: 10px 30px; }
    `]
})
export class PlanDetailsComponent implements OnInit {


    planDetails$: Observable<any>;

    constructor(private store: Store<fromRoot.State>){
        this.planDetails$ = store.select(fromRoot.getPlanDetails);
    }

    ngOnInit(){

    }
}



