import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

@Component({
    selector: 'selectunits-tabs',
    template: `<ul class="tabNav">
          				<li (click)="onTab('U')" [ngClass]="{'selected': isPropertySelected}"> Property Units</li>
          				<li *ngIf="isPlanDetailsAvailable" (click)="onTab('P')" [ngClass]="{'selected': isPlanDtlsSelected}"> Plan Details</li>
		           </ul>`
    //styleUrls: ['./confirm.component.css']
})

export class SelectUnitsTabComponent implements OnInit {

    @Output() onTabClick = new EventEmitter<any>();
    @Input() isPlanDetailsAvailable: boolean;
    isPlanDtlsSelected: boolean = false;
    isPropertySelected: boolean = false;

    onTab(val){
        this.isPlanDtlsSelected = (val == 'P');
        this.isPropertySelected = (val == 'U');
        this.onTabClick.emit(val);
    }

    ngOnInit(){
        this.isPropertySelected = true;
        this.onTabClick.emit('U');
    }

}



